
<!--
        -------------------------------------------------------List.vue 
        <template>
            <div>
                <slot v-for="item in ds" :item="item"></slot>
            </div>
        </template>
        --script--
        export default{
            props:["ds"]
        }
        --script-- 
-->
<template>
    <div>
        <raw-loader :mytxt="require('!raw-loader!./SlotLaGi.vue').default"></raw-loader>
        <h3>hello world read it every day</h3>
        <h3>Vi du 1</h3>
        <list :ds="[{Id:1,Ten:'thuc'},{Id:2,Ten:'thuc'}]">
                <div slot-scope="row">
    				{{ row }}
    			</div>
        </list>
        <hr>
        <h3>Vi du 2</h3>
        <list
            :ds="[{id:1,ten:'thuc'},{id:2,ten:'thuc'}]" >
                <template v-slot:tieude >
                    <h3>hello world</h3>
                </template>
                <div slot-scope="row">
    				{{row.item.id}} {{ row.item.ten }}
    			</div>
        </list>
        <hr>
        <h3>Vi du 3</h3>
        <list :ds="[{id:1,ten:'thuc'},{id:2,ten:'thuc'}]">
            <template slot-scope="row" >
                <div v-for="x in row" :key="x.Id">{{ x }}</div>
            </template>
        </list>
        <hr>
        <h3>Vi du 4</h3>
        <ul>
            <list :ds="[{id:1,ten:'thuc'},{id:2,ten:'thuc'}]">
                <li slot-scope="row">{{row.item.id}} {{ row.item.ten }}</li>
            </list>
        </ul>
        <h3>Vi du 5</h3>
        <table border="1">
            <list :ds="[{id:1,ten:'thuc'},{id:2,ten:'thuc'}]">
                    <template v-slot:tieude >
                        <h3>hello world</h3>
                    </template>
                    <tr slot-scope="row">
                        <td>{{ row.item.id }}</td>
                        <td>{{ row.item.ten }}</td>
                        <td>
                            <button @click="onEdit(row)">Edit</button>
                            <button>Del</button>
                        </td>
                    </tr>
            </list>
        </table>
    </div>
</template>
<script>
import List from "@/views/BTable4/List.vue"
export default{
    props:["ds"],
    components:{
        List
    },
    methods:{
        onEdit(row){
            alert(JSON.stringify(row));
        }
    }
}
</script>
